<template>
    <div>
        <loading :active.sync="isLoading"></loading>
        <div class="text-right">
            <button class="btn btn-primary mt-4" @click="openModal(true)"></button>
        </div>
            <table class="table mt-4">
                <thead>
                    <tr>
                        <th width="120"></th>
                        <th></th>
                        <th width="120"></th>
                        <th width="120"></th>
                        <th width="100"></th>
                        <th width="120"></th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(item) in products" :key="item.id">
                        <td>{{item.category}}</td>
                        <td>{{item.title}}</td>
                        <td class="text-right">{{item.origin_price | currency }}</td>
                        <td class="text-right">{{item.price | currency }}</td>
                        <td>
                            <span class="text-success" v-if="item.is_enabled == 1"></span>
                            <span v-if="item.is_enabled !== 1"></span>
                        </td>
                        <td align="right" class="d-flex">
                            <button class="btn btn-outline-primary btn-sm" @click="openModal(false,item)"></button>
                            <button class="btn btn-outline-warning btn-sm text-danger" @click="delProductModal(item)"></button>                          
                        </td>
                    </tr>
                </tbody>
            </table>
            <pagination :pagination="pagination" @pageChange="getProduct"></pagination>
            <div class="modal fade" id="productModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                <div class="modal-dialog modal-lg" role="document">
                    <div class="modal-content border-0">
                        <div class="modal-header bg-dark text-white">
                            <h5 class="modal-title" id="exampleModalLabel">
                                <span v-if="isNew"></span>
                                <span v-else></span>
                            </h5>
                            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                        <div class="modal-body">
                            <div class="row">
                                <div class="col-sm-4">
                                    <div class="form-group">
                                    <label for="image"></label>
                                    <input type="text" v-model="tempProduct.imageUrl" class="form-control" id="image" placeholder="">
                                </div>
                                <div class="form-group">
                                     <label for="customFile"> 
                                        <i class="fas fa-spinner fa-spin" v-if="status.fileUploading"></i>
                                    </label>
                                    <input type="file" id="customFile" class="form-control" ref="files" @change="uploadFile">
                                </div>
                                <img img="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80"
                                class="img-fluid" :src="tempProduct.imageUrl" alt="">
                            </div>
                            <div class="col-sm-8">
                                <div class="form-group">
                                    <label for="title"></label>
                                    <input type="text" v-model="tempProduct.title" class="form-control" id="title" placeholder="">
                                </div>
                                <div class="form-row">
                                    <div class="form-group col-md-6">
                                        <label for="category"></label>
                                        <input type="text" v-model="tempProduct.category" class="form-control" id="category" placeholder="">
                                    </div>
                                    <div class="form-group col-md-6">
                                        <label for="price"></label>
                                        <input type="unit" v-model="tempProduct.unit" class="form-control" id="unit" placeholder="">
                                    </div>
                                </div>
                                <div class="form-row">
                                    <div class="form-group col-md-6">
                                        <label for="origin_price"></label>
                                        <input type="number" v-model="tempProduct.origin_price" class="form-control" id="origin_price" placeholder="">
                                    </div>
                                    <div class="form-group col-md-6">
                                        <label for="price"></label>
                                        <input type="number" class="form-control" v-model="tempProduct.price" id="price" placeholder="">
                                    </div>
                                </div>
                                <hr>
                                <div class="form-group">
                                    <label for="description"></label>
                                    <textarea type="text" class="form-control" v-model="tempProduct.description" id="description" placeholder=""></textarea>
                                </div>
                                <div class="form-group">
                                    <label for="content"></label>
                                    <textarea type="text" class="form-control" v-model="tempProduct.content" id="content" placeholder=""></textarea>
                                </div>
                                <div class="form-group">
                                    <div class="form-check">
                                        <input class="form-check-input" type="checkbox" id="is_enabled" 
                                        v-model="tempProduct.is_enabled" :true-value="1" :false-value="0">
                                        <label class="form-check-label" for="is_enabled"></label>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
                        <button type="button" class="btn btn-primary" @click="updateProduct"></button>
                    </div>
                    </div>
                </div>
            </div>

            <div class="modal fade" id="delProductModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                <div class="modal-dialog" role="document">
                    <div class="modal-content border-0">
                        <div class="modal-header bg-danger text-white">
                            <h5 class="modal-title" id="exampleModalLabel">
                                <span></span>
                            </h5>
                            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                        <div class="modal-body">
                             <strong class="text-danger">{{ tempProduct.title }}</strong> ()
                        </div>
                        <div class="modal-footer">
                            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
                            <button type="button" class="btn btn-danger" @click="deleteProduct"
                            ></button>
                        </div>
                    </div>
                </div>
            </div>
    </div>
</template>
<script>
import $ from 'jquery';
import Pagination from '@/components/pagination'

export default {
    components:{
        Pagination,
    },
    data(){
        return {
            products:[],
            pagination:{},
            tempProduct:{},
            isNew:false,
            isLoading:false,
            status:{
                fileUploading:false,
            }
        }
    },
    methods:{
        getProduct(page = 1){
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/products?page=${page}`
            const vm = this;
            vm.isLoading = true;
            this.$http.get(api).then((response) => {
            console.log(response.data);
            vm.isLoading = false;
            vm.products = response.data.products;
            vm.pagination = response.data.pagination;
            })
        },
        openModal(New,item){

            if(New){
                this.tempProduct = {};
                this.isNew = true;
            }else{
                this.tempProduct = Object.assign({},item);
                this.isNew = false;
            };
            $('#productModal').modal('show');
        },
        updateProduct(){
            let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product`;
            let httpMethod = 'post'
            const vm = this;
            if(!vm.isNew){
                api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
                httpMethod = 'put'
            }
            this.$http[httpMethod](api,{data: vm.tempProduct}).then((response) => {
                console.log(response.data);
                if(response.data.success){
                    $('#productModal').modal('hide');
                    vm.getProduct();
                }else{
                    $('#productModal').modal('hide');
                    vm.getProduct();
                    cosole.log('');
                }
            })
        },
        delProductModal(item){
            $('#delProductModal').modal('show')
             this.tempProduct = Object.assign({},item);  
        },
        deleteProduct(){
            const vm = this;
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
            this.$http.delete(api).then((response) => {
                console.log(response.data);
                $('#delProductModal').modal('hide');
                vm.getProduct();
            })
        },
        uploadFile(){
            const uploadedFile = this.$refs.files.files[0];
            const vm = this;
            const formData = new FormData();//web api
            formData.append('file-to-upload', uploadedFile);
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/upload`;
            vm.status.fileUploading = true;
            this.$http.post(url,formData,{
                headers:{
                    'Content-Type':'multipart/form-data'
                }
            }).then((response) => {
                console.log(response.data);   
                vm.status.fileUploading = false;
                if(response.data.success){
                    // vm.tempProduct.imageUrl = response.data.imageUrl //$set
                    vm.$set(vm.tempProduct, 'imageUrl', response.data.imageUrl)
                }else{
                    this.$bus.$emit('message:push', response.data.message, 'danger')
                }         
            })
        }
    },
    created(){
        this.getProduct();
        
    }
}
</script>




